.headertext {
    background: rgb(0, 0, 0);
    letter-spacing: 7px;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Staatliches', cursive;
    font-size: 6em;
    text-transform: uppercase;
    text-shadow: 4px 3px 0px white;
    margin-top: 0;
}

h1.headertext {
    margin-top: 0;
}

.nav {
    background-color: rgba(0, 0, 0, 0.952);
    position: relative;
    bottom: -40px;
}
.map {
    width: 100%;
    height:400px;
  }

.worldMap {
    width: 100%;
    height:900px;
  }

.mapLabelArea {
    display: none;
}

.marker {
    width: 20px;
    height: 20px;
    border: 1px solid #088;
    border-radius: 10px;
    background-color: #0FF;
    opacity: 0.5;
}

.smallMarker {
    width: 10px;
    height: 10px;
    border: 1px solid #088;
    border-radius: 5px;
    background-color: #0FF;
    opacity: 0.5;
}

[data-title]:hover:after {
    opacity: 1;
    transition: all 0.1s ease 0.5s;
    visibility: visible;
}
[data-title]:after {
    content: attr(data-title);
    background-color: #00FF00;
    color: #111;
    font-size: 11pt;
    position: absolute;
    padding: 1px 5px 2px 5px;
    bottom: -1.6em;
    left: 100%;
    white-space: pre;
    box-shadow: 1px 1px 3px #222222;
    opacity: 0;
    border: 1px solid #111111;
    z-index: 99999;
    visibility: hidden;
}
[data-title] {
    position: relative;
}

.markerText {
    text-decoration: none;
    color: white;
    font-size: 11pt;
    font-weight: bold;
    text-shadow: black 0.1em 0.1em 0.2em;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(54, 54, 54);
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.centerFlex {
  padding: 20px 0;
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
      to right bottom,
      rgba(2, 2, 2, 0.3),
      rgba(0, 0, 0, 0.856)
    ),
    url(https://images.unsplash.com/photo-1451187580459-43490279c0fa?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80);
  background-repeat: no-repeat;
  background-size: cover;
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background-origin: content-box;
  background-attachment: fixed;
  background-position: center;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f46732', endColorstr='#66fcb46f', GradientType=0);
  /* IE6-9 */
}

